.center-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.page-body {
  font-family: "Poppins Medium", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #1d1613;
  height: 100%;
}

.page-header {
  margin: 0;
  padding: 17px 50px 0;
}
@media (max-width: 1023px) {
  .page-header {
    padding: 27px 45px 0;
  }
}
@media (max-width: 767px) {
  .page-header {
    padding: 10px 15px 0;
  }
}

.page-header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1168px;
  margin: 0 auto 26px;
  padding: 0;
}

.page-header__search-block {
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__search-block {
    display: none;
  }
}

.page-header__search-inner {
  position: relative;
  margin: 0;
  padding: 0;
}
.page-header__search-inner::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 0;
  background: url("../img/decor/magnifier.svg") no-repeat;
  background-size: 14px;
  width: 15px;
  height: 15px;
}

.page-header__search-block input {
  border: 0;
  padding: 0 0 0 30px;
}
.page-header__search-block input:hover {
  outline: 1px solid black;
  transition: 0.2s;
}
.page-header__search-block input::placeholder {
  color: #9b9997;
  font-size: 14px;
  line-height: 21px;
}

.page-header__logo {
  z-index: 1;
  transform: translate(-58px, 13px);
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__logo {
    transform: translate(-5px, 3px);
  }
}

.page-header__logo svg {
  width: 66px;
  height: 66px;
  transform: rotate(-360deg);
  transition: 0.5s;
}
@media (max-width: 767px) {
  .page-header__logo svg {
    transform: scale(0.8);
  }
}

@media (max-width: 1023px) {
  .page-header__logo.page-header__logo--change-color svg {
    stroke: white;
    transform: rotate(360deg);
    transition: 0.5s;
  }
}
@media (max-width: 767px) {
  .page-header__logo.page-header__logo--change-color svg {
    transform: scale(0.8) rotate(360deg);
  }
}

.page-header__login-block {
  display: flex;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__login-block::before {
    content: "";
    display: block;
    -webkit-mask: url("../img/decor/bag.svg") no-repeat 50% 50%;
    mask: url("../img/decor/bag.svg") no-repeat 50% 50%;
    -webkit-mask-size: cover;
    mask-size: cover;
    background-size: 18px 16px;
    width: 18px;
    height: 16px;
    background-color: #1d1613;
    transition: 1s;
  }
}

@media (max-width: 1023px) {
  .page-header__login-block.page-header__login-block--change-bag::before {
    background-color: white;
    transition: 1s;
  }
}

.page-header__login-block p {
  font-size: 14px;
  line-height: 21px;
  margin: 0;
  padding: 0;
}
.page-header__login-block p:first-child {
  margin-right: 35px;
}
@media (max-width: 1023px) {
  .page-header__login-block p {
    display: none;
  }
}

.page-header__login-block a {
  color: #1d1613;
}
.page-header__login-block a:hover {
  color: #a87b62;
  transition: 0.2s;
}
.page-header__login-block a:active {
  opacity: 0.6;
}

.page-header__tablet {
  display: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__tablet {
    justify-content: space-between;
    align-items: center;
  }
}

@media (max-width: 1023px) {
  .page-header__tablet-disappear {
    display: flex;
  }
}

@media (max-width: 1023px) {
  .page-header__tablet-inner {
    margin: 0;
    padding: 21px 0 0 21px;
  }
}
@media (max-width: 767px) {
  .page-header__tablet-inner {
    padding: 20px 0 0 17px;
  }
}

.page-header__tablet-inner svg {
  background-size: 68px 21px;
  width: 68px;
  height: 21px;
}

.navigation {
  border-top: 1px solid #f6f5f4;
  margin: 0;
  padding: 18px 0 0 0;
}
@media (max-width: 1023px) {
  .navigation {
    display: none;
  }
}

.navigation ul {
  display: flex;
  max-width: 481px;
  list-style: none;
  margin: 0 auto;
  padding: 0;
}

.navigation li {
  white-space: nowrap;
  margin: 0 35px 0 0;
  padding: 0;
}
.navigation li:last-child {
  margin: 0;
}

.navigation a {
  color: #1d1613;
}
.navigation a:hover {
  color: #a87b62;
  transition: 0.2s;
}
.navigation a:active {
  opacity: 0.6;
}

.burger-closed {
  display: none;
  cursor: pointer;
  transition: 1s all;
  z-index: 1;
}
@media (max-width: 1023px) {
  .burger-closed {
    display: none;
    justify-content: center;
    align-items: center;
    min-width: 25px;
    min-height: 16px;
    background-color: transparent;
    margin: 0;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .burger-closed {
    min-width: 25px;
    min-height: 16px;
  }
}

.burger-closed--change-display {
  display: flex;
}

.burger-closed--background-color {
  background-color: transparent;
}

.burger-closed__inner {
  overflow: hidden;
  min-width: 22px;
  min-height: 22px;
}

.burger-closed__top-line {
  border-bottom: 2px solid #1d1613;
  width: 23px;
  padding: 0;
  margin: 0 0 7px 0;
  transition: 1s;
}

.burger-closed__middle-line {
  border-bottom: 2px solid #1d1613;
  width: 23px;
  padding: 0;
  margin: 0 0 7px 0;
  transition: 1s;
}

.burger-closed__bottom-line {
  transition: 1s;
}

.burger-closed__line {
  border-bottom: 2px solid #1d1613;
  width: 23px;
}

.burger-closed__line--active {
  transform: translate(200%);
  transition: 1s;
  margin: 0;
  padding: 0;
}

.burger-closed__top-line--transform {
  border-bottom: 2px solid white;
}

.burger-closed__middle-line--transform {
  border-bottom: 2px solid white;
}

.burger-closed__bottom-line--transform {
  border-bottom: 2px solid white;
}

.burger-open {
  display: none;
}
@media (max-width: 1023px) {
  .burger-open {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    min-height: 500%;
    transition: 1s;
    background-color: #a87b62;
  }
}

.burger-open--appear {
  display: block;
  padding: 0 45px;
}
@media (max-width: 767px) {
  .burger-open--appear {
    padding: 0 15px;
  }
}

.burger-open__menu {
  width: 50px;
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin: 0 0 100px auto;
  padding: 0 20px 0 0;
}
@media (max-width: 1023px) {
  .burger-open__menu {
    margin: 0 0 85px auto;
  }
}
@media (max-width: 767px) {
  .burger-open__menu {
    margin: 0 0 50px auto;
  }
}

.burger-open__list {
  height: 770px;
  overflow: scroll;
}

.burger-open__list a {
  color: white;
}

.burger-open__list-item-wrapper {
  position: relative;
  background-color: red;
  margin: 0 0 30px 0;
  padding: 0;
  max-height: 60px;
}
.burger-open__list-item-wrapper:hover {
  background-color: red;
}
.burger-open__list-item-wrapper:active {
  background-color: red;
}
.burger-open__list-item-wrapper:last-child {
  margin: 0;
}

.burger-open__list-item {
  margin: 0;
  padding: 0;
  position: relative;
}

.burger-open__list-item a {
  display: block;
  font-size: 20px;
  line-height: 28px;
  color: #9b9997;
  padding: 15px;
}

.burger-open__action {
  display: none;
}

.burger-open__search {
  position: relative;
  margin: 0 0 42px;
  padding: 0;
}
@media (max-width: 1023px) {
  .burger-open__search::before {
    content: "";
    position: absolute;
    top: 11px;
    left: 11px;
    -webkit-mask: url("../img/decor/magnifier.svg") no-repeat 50% 50%;
    mask: url("../img/decor/magnifier.svg") no-repeat 50% 50%;
    -webkit-mask-size: cover;
    mask-size: cover;
    background-size: 18px 17px;
    width: 18px;
    height: 17px;
    background-color: white;
  }
}
@media (max-width: 767px) {
  .burger-open__search {
    margin: 0 0 34px;
  }
}

.burger-open__search input {
  min-width: 100%;
  min-height: 40px;
  background-color: white;
  opacity: 0.15;
  border: 0;
  outline: 0;
  border-radius: 21px;
  padding: 0 0 0 50px;
}

.burger-open__list-nav {
  margin-bottom: 37px;
}
@media (max-width: 767px) {
  .burger-open__list-nav {
    margin-bottom: 26px;
  }
}

.burger-open__list-nav p {
  margin: 0 0 22px;
  padding: 0;
}
.burger-open__list-nav p:last-child {
  margin: 0;
}

.burger-open__list-nav a {
  font-family: "Poppins Medium", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #1d1613;
  font-size: 24px;
  line-height: 36px;
  color: white;
}

.burger-open__list-contacts {
  margin-bottom: 42px;
}
@media (max-width: 767px) {
  .burger-open__list-contacts {
    margin-bottom: 34px;
  }
}

.burger-open__list-contacts p {
  margin: 0 0 19px;
  padding: 0;
}
.burger-open__list-contacts p:last-child {
  margin: 0;
}

.burger-open__list-contacts a {
  font-family: "Poppins Medium", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #1d1613;
  font-size: 18px;
  line-height: 25px;
  opacity: 0.75;
  color: white;
}

.burger-open__list-login p {
  margin: 0;
  padding: 0;
}

.burger-open__list-login a {
  font-family: "Poppins Medium", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #1d1613;
  font-size: 18px;
  line-height: 24px;
  color: white;
}

.page-main {
  flex: 1;
  margin: 0;
  padding: 0;
}

.page-footer {
  min-height: 338px;
  background-color: black;
  margin: 0;
  padding: 97px 50px 0;
}
@media (max-width: 1023px) {
  .page-footer {
    min-height: 446px;
    padding: 57px 42px 0;
  }
}
@media (max-width: 767px) {
  .page-footer {
    padding: 57px 12px 0;
    min-height: 773px;
  }
}

.page-footer__inner {
  display: flex;
  justify-content: space-between;
  max-width: 1174px;
  margin: 0 auto;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-footer__inner {
    display: block;
  }
}

.sign-up {
  min-width: 43%;
  transform: translateY(-6px);
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .sign-up {
    margin-bottom: 39px;
  }
}
@media (max-width: 767px) {
  .sign-up {
    margin-bottom: 40px;
  }
}

.sign-up > p {
  font-family: "Cormorant Garamond Bold", "Arial", sans-serif;
  font-size: 45px;
  line-height: 59px;
  color: white;
  font-size: 34px;
  line-height: 44px;
  color: white;
  letter-spacing: 0.03em;
  margin: 0 0 8px;
  padding: 0;
}
.sign-up > p:last-of-type {
  font-family: "Poppins Regular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #9b9997;
  font-size: 15px;
  color: #9b9997;
  text-indent: 5px;
  margin: 0 0 45px;
}
@media (max-width: 767px) {
  .sign-up > p:last-of-type {
    text-indent: unset;
  }
}
@media (max-width: 325px) {
  .sign-up > p {
    max-width: 240px;
  }
}

.sign-up__input-wrapper {
  display: flex;
  justify-content: flex-start;
  border-bottom: 1px solid white;
  max-width: 566px;
  margin: 0;
  padding: 0 0 8px;
}

.sign-up__input-wrapper input {
  flex: 1;
  background-color: transparent;
  border: 0;
  color: white;
}
.sign-up__input-wrapper input:hover {
  outline: 1px solid white;
}
.sign-up__input-wrapper input::placeholder {
  color: #9b9997;
  font-size: 14px;
}

.sign-up__input-wrapper p {
  text-indent: 5px;
  margin: 0;
  padding: 0;
}

.sign-up__input-wrapper a {
  position: relative;
  white-space: nowrap;
  color: white;
  margin: 0;
  padding: 10px 0 10px 10px;
}
.sign-up__input-wrapper a:hover {
  opacity: 0.6;
  transition: 0.2s;
  color: #a87b62;
}
.sign-up__input-wrapper a:active {
  opacity: 0.4;
}
.sign-up__input-wrapper a::after {
  content: "";
  display: inline-block;
  -webkit-mask: url("../img/decor/arrow.svg") no-repeat 50% 50%;
  mask: url("../img/decor/arrow.svg") no-repeat 50% 50%;
